export default {
	data() {
		return {
			email:"",
			password:""
		}
	},
	computed: {
		disabled() {
			return this.email == '' || this.password == '' 
		}
	},
	methods: {
		// 验证
		check(){
			let rule = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
			if (!rule.test(this.email)) {
				uni.showToast({
					title:"邮箱格式不正确",
					icon:"none"
				})
				return false;
			}
			return true
		},
		// 提交
		submit(){
			if (!this.check()) {
				return;
			}
			console.log("提交成功")
		}
	}
}
